.hero {
    height: 80vh;
    background: linear-gradient(180deg, #fde1ff, #e1ffea22 60%);
    display: flex;
}

.hero-left {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 30px;
  padding-left: 180px;
  line-height: 1.1;
}

.hero-left h2 {
    color: #090909;
    font-size: 26px;
    font-weight: 600;
}

.hero-left p {
    color: #171717;
    font-size: 70px; /*was 100px*/
    font-weight: 700;
}

.hero-hand-icon {
    display: flex;
    align-items: center;
    gap: 20px;
}

.hero-hand-icon img {
    width: 105px;
}

.hero-latest-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;
    width: 310px;
    height: 70px;
    border-radius: 75px;
    margin-top: 30px;
    background: #ff4141;
    color:#fff;
    font-size: 22px;
    font-weight: 500;
    cursor: pointer;
}

.hero-right {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
}

.hero-right img {
    width: 500px;
}

@media(max-width: 1280px) {
    .hero-left {
        padding-left: 100px;
    }
    .hero-left h2 {
        font-size: 22px;
    }
    .hero-left p {
        font-size: 60px;
    }
    .hero-hand-icon img {
        width: 80px;
    }
    .hero-latest-btn {
        gap: 10px;
        width: 250px;
        height: 60px;
        margin-top: 20px;
        font-size: 18px;
    }
    .hero-right img {
       width: 450px;
    }
}

@media(max-width: 1024px) {
    .hero-left {
        padding-left: 80px;
    }
    .hero-left h2 {
        font-size: 20px;
    }
    .hero-left p {
        font-size: 50px;
    }
    .hero-hand-icon img {
        width: 65px;
    }
    .hero-latest-btn {
        gap: 5px;
        width: 220px;
        height: 50px;
        margin-top: 10px;
        font-size: 18px;
    }
    .hero-right img {
       width: 400px;
    }
}


@media(max-width: 800px){
    .hero-left {
        padding-left: 50px;
    }
    .hero-left h2 {
        font-size: 20px;
    }
    .hero-left p {
        font-size: 40px;
    }
    .hero-hand-icon img {
        width: 60px;
    }
    .hero-latest-btn {
        gap: 10px;
        width: 175px;
        height: 40px;
        font-size: 13px;
    }
    .hero-right img {
       width: 300px;
    }
}


@media(max-width: 500px){
    .hero {
        flex-direction: column;
        align-items: center;
    }
    .hero-right {
        display: none;
    }
    .hero-left h2 {
        font-size: 18px;
    }
    .hero-left p {
        font-size: 40px;
    }
    .hero-hand-icon img{
        width: 55px;
    }
    .hero-latest-btn {
        width: 220px;
        height: 55px;
        font-size: 16px;
    }
}
